export default {
	name: 'comment-item',
	props: {
		dataList: {
			// 要显示的数组
			type: Array,
			default() {
				return [];
			}
		},
		isDetail: { // 是否是详情
			type: Boolean,
			default() {
				return false;
			}
		}
		// showOper:{ // 距离头部多少px将其固定
		//   type: Boolean,
		//   default(){
		//     return false;
		//   }
		// }
	},
	data() {
		return {};
	},
	methods: {
		showAll(ind) {
			this.dataList[ind].showMore = true;
		},
		hideMore(ind) {
			this.dataList[ind].showMore = false;
		},
		showBigImg(listInd, imgInd) {
			let info = this.dataList[listInd];
			let imgList = info.imgList;
			this.$acFrame.Util.showBigPic(imgList[imgInd], imgList);
			this.showPic = true;
		},
		loadMoreData() {},
		linkDetail(){
			if(!this.isDetail) {
				uni.navigateTo({
					url:'/pages/index/commentDetail'
				})
			}
			
		}
	},
	watch: {}
};
